// File: /app/components/global/MediaManager/MediaGrid.js
// Description: Fetches and displays a grid of media items from WordPress

'use client'

import React, { useEffect, useState } from 'react'
import { getAllMedia } from '@/app/media/mediaActions'
import MediaItemCard from './MediaItemCard'

// Displays the media library grid
export default function MediaGrid() {
  const [media, setMedia] = useState([])     // Fetched media list
  const [loading, setLoading] = useState(true)

  // Load media on component mount
  useEffect(() => {
    const loadMedia = async () => {
      setLoading(true)
      try {
        const mediaItems = await getAllMedia({ per_page: 40 }) // Optional: add filters
        setMedia(mediaItems)
      } catch (err) {
        console.error('Failed to load media', err)
      }
      setLoading(false)
    }

    loadMedia()
  }, [])

  return (
    <div>
      {/* Loading state */}
      {loading && <p className="text-sm text-gray-500">Loading media...</p>}

      {/* Empty state */}
      {!loading && media.length === 0 && (
        <p className="text-sm text-gray-500">No media found.</p>
      )}

      {/* Media grid */}
      <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-5 gap-4">
        {media.map((item) => (
          <MediaItemCard key={item.id} media={item} />
        ))}
      </div>
    </div>
  )
}
