// File: /app/components/global/MediaManager/MediaManagerProvider.js
// Description: Context provider to control global media modal state, selection, and interaction

'use client'

import React, { createContext, useState, useCallback } from 'react'

// Context object used by the modal and trigger components
export const MediaManagerContext = createContext()

// Provider component to wrap app or page
export function MediaManagerProvider({ children }) {
  const [isOpen, setIsOpen] = useState(false)           // Modal visibility
  const [selectedMedia, setSelectedMedia] = useState(null) // Selected media object

  // Open modal
  const openModal = useCallback(() => setIsOpen(true), [])

  // Close modal and clear selection
  const closeModal = useCallback(() => {
    setIsOpen(false)
    setSelectedMedia(null)
  }, [])

  // Select a media item
  const selectMedia = useCallback((media) => {
    setSelectedMedia(media)
  }, [])

  return (
    <MediaManagerContext.Provider
      value={{
        isOpen,
        openModal,
        closeModal,
        selectedMedia,
        selectMedia,
      }}
    >
      {children}
    </MediaManagerContext.Provider>
  )
}
